<template>
  <div class="hs-container">
    <c-header :title="$t('base.ForgetPassword')" isBackNav></c-header>
    <div class="title">
      <h1>{{$t('base.SecondStep')}}</h1>
      <p>{{$t('base.WeHaveSent')}}<span v-if="details">{{details.Mobile}}</span>{{$t('base.AVerificationMessagePleaseEnterTheVerificationCodeYouReceived')}}</p>
    </div>
    <div class="information">
      <input maxlength='50' v-model="receivedCode" :placeholder="$t('base.VerificationCode')"/>
      <div class="send send-disable" v-if="timeSecond > 0"><p>{{$t('base.Resend')}}</p><p>({{timeSecond}}s)</p></div>
      <div class="send" v-else @click="_sendMessage">{{$t('base.Resend')}}</div>
    </div>
    <hs-gradient-button style="margin-top: 50px" :title="$t('base.NextStep')" @click="_nextStep"></hs-gradient-button>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import {kpiUserGetForgotPasswordCode, kpiUserCheckVerificationCode} from 'api'
export default {
  created() {
    this._setCountDown()
  },
  computed: {
    details() {
      return JSON.parse(this.$route.query.details)
    }
  },
  data() {
    return {
      timeSecond: 0,
      receivedCode: ''
    }
  },
  methods: {
    _setCountDown() {
      this.timeSecond = 60
      let myInterval = setInterval(() => {
        if (--this.timeSecond <= 0) {
          clearInterval(myInterval)
        }
      }, 1000)
    },
    _sendMessage() {
      this._setCountDown()
      kpiUserGetForgotPasswordCode(this.details).then((res) => {
        console.log('获取忘记密码验证码: ', res)
        // this.details.Code = res.ResultData
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.SendSucceed'))
        }
      })
    },
    _nextStep() {
      let params = {
        Mobile: this.details.Mobile,
        Code: this.receivedCode
      }
      kpiUserCheckVerificationCode(params).then((res) => {
        console.log('校验验证码有效性: ', res)
        if (res.ResultData === true) {
          this.details.Code = this.receivedCode
          this.$router.push({path: '/forgetPasswordStep3', query: {details: JSON.stringify(this.details)}})
        }
      })
    }
  },
  components: {
    CHeader,
    HsGradientButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.title
    width 100%
    padding 0 10px
    >h1
      font-size 1rem
      font-weight bold
      margin-top 10px
    >p
      font-size 0.9rem
      margin-top 5px
      color $color_black_text_1
      >span
        color $color_theme
  >.information
    @extend .flex-row
    @extend .flex-between
    width 100%
    padding 0 10px
    margin-top 20px
    height 40px
    >input
      width calc(100% - 90px)
      height 100%
      border 1px solid $color_gray_layer
      border-radius 4px
      outline none
    >.send
      @extend .flex-column
      @extend .flex-center
      width 80px
      height 100%
      border-radius 4px
      background-color $color_theme
      color $color_white
      font-size 0.9rem
      line-height 1.4em
    >.send-disable
      font-size 0.8rem
      background-color $color_gray_text
</style>



